import { GSPBusinessEntity } from '@gsp-bef/gsp-be-metadata';

import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MappingRuleService } from '../../mapping-rule.service';
import { MappingUnit } from '../../model/mapping-unit';
import { BEToDemUtil } from '../../util/beToDem';
import { SourceType } from '../../model/mapping-model-source';
import { isNullOrUndefined } from 'util';

import { Metadata4RefDto } from '@gsp-lcm/metadata-selector';
@Component({
    selector: 'app-mapping-select-srcdem',
    templateUrl: './mapping-select-srcdem.component.html',
    styleUrls: ['./mapping-select-srcdem.component.css']
})
export class MappingSelectSrcDemComponent implements OnInit {
    @Output() closeModal = new EventEmitter<any>();
    @Input() value;
    modalConfig = {
        title: '',
        width: 901,
        height: 612
    };
    get sourcetype() {
        return 'GSPBusinessEntity';
    }
    path: String = ''; // test\\be
    bename: String = '';
    private objId: String = '';
    get metadataPath() {
        const metadata = this.mappingservice.mappingMetadata;
        if (isNullOrUndefined(metadata) === false) {
            return metadata.relativePath;
        } else {
            return '';
        }
        // .relativePath;
    }
    constructor(private mappingservice: MappingRuleService) { }

    ngOnInit() {
    }
    getBeMetadata(beMetadata: Metadata4RefDto) {
        if (isNullOrUndefined(beMetadata) === true) {
            alert('');
            return;
        }
        const be = JSON.parse(beMetadata.metadata.content) as GSPBusinessEntity;
        console.log('bedtata:', be);
        const dem = BEToDemUtil.beToDem(be);
        console.log('dem', dem);
        if (this.mappingservice.currentTreeNode.level === 1) {
            this.mappingservice.srcBe = be;
            // TODO 
        }
        // const be = JSON.parse(value.content) as GSPBusinessEntity;
        // console.log('bedtata:', be);
        dem.Source.SourcePackageName = beMetadata.packageHeader.name;
        if (isNullOrUndefined(dem.Source.SourcePackageName)) {
            dem.Source.SourcePackageName = '';
        }
        // TODO 
        dem.Source.SourcePackageName = beMetadata.metadata.relativePath;
        // TODO ?
        dem.Source.SourceMetadataID = be.ID;
        // TODO 
        dem.Source.SourceType = SourceType.beType;

        const mappingunit = this.mappingservice.currentTreeNode.mappingNode as MappingUnit;
        const srcDemId = mappingunit.SrcDemId;
        mappingunit.SrcDemId = dem.ID;
        this.mappingservice.addMappingRuleDem(dem);
        this.mappingservice.removeMappingRuleDem(srcDemId);
        this.value = dem.ID;
        this.closeModal.emit(this.value);
    }
    cancel() {
        this.closeModal.emit();
    }
}



