import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { ColumnMapping } from './../model/column-mapping';
import { MappingUnit } from '../model/mapping-unit';
import { isNullOrUndefined } from 'util';
import { MappingRule } from '../model/mapping-rule';
/**
 * 
 */
export class MappingValidate {
    /**
     *  
     */
    public static checkMappingRule(rule: MappingRule): boolean {
        if (isNullOrUndefined(rule.RootMappingUnitList) === false) {
            for (const childUnit of rule.RootMappingUnitList) {
                if (this.checkMappingUnit(childUnit) === false) {
                    return false;
                }
            }
        }
        return true;
    }
    /**
     *  
     */
    public static checkMappingUnit(unit: MappingUnit): boolean {
        if (this.checkColumnMappings(unit.Name, unit.ColumnMappingList) === false) {
            return false;
        }
        if (isNullOrUndefined(unit.ChildrenMU) === false) {
            for (const childUnit of unit.ChildrenMU) {
                if (this.checkMappingUnit(childUnit) === false) {
                    return false;
                }
            }
        }
        return true;
    }
    /**
     * 
     */
    public static checkColumnMappings(unitName: string, colMappings: ColumnMapping[]): boolean {
        if (isNullOrUndefined(colMappings) === false) {
            const colMaps = colMappings.filter(t => t.IsSelect === true);
            for (const colMap of colMaps) {
                if (isNullOrEmptyString(colMap.Expression) === true) {
                    alert('' + unitName + '' + colMap.ExeOrder + ',');
                    return false;
                } else {

                }
            }
        }
        return true;
    }
}



