
import { DataExchangeObjectTree } from './model/clientmodel/dataexchangeobject-tree';
import { ColumnMapping } from './model/column-mapping';
import { DataExchangeElement } from './model/dataexchange-element';
import { MappingUnit } from './model/mapping-unit';
import { MappingTree } from './model/clientmodel/mapping-tree';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { DataExchangeModel } from './model/dataexchange-model';
import { isNullOrUndefined, error } from 'util';
import { DataExchangeObject } from './model/dataexchange-object';
import { MappingRule } from './model/mapping-rule';
import { Injectable } from '@angular/core';
import { GspElementTypes } from './util/gsp-element-type';
import { GSPBusinessEntity } from '@gsp-bef/gsp-be-metadata';
import { MetadataDto } from '@gsp-lcm/metadata-selector';
import { SrcColumn } from './model/clientmodel/srccolumn';
@Injectable()
export class MappingRuleService {
  mappingMetadata: MetadataDto;

  private _mappingRule: MappingRule;
  /**
   * 
   */
  public get mappingRule(): MappingRule {
    return this._mappingRule;
  }
  /**
   * 
   */
  public set mappingRule(v: MappingRule) {
    this._mappingRule = v;
  }

  public mappingTree = new MappingTree();
  public currentTreeNode: MappingTree;
  // colmappings = new Array<ColumnMapping>();
  get colMappings(): ColumnMapping[] {
    if (isNullOrUndefined(this.currentTreeNode) === true) {
      return new Array<ColumnMapping>();
    }
    const unit = this.currentTreeNode.mappingNode as MappingUnit;
    let colMappings = unit.ColumnMappingList;
    if (isNullOrEmptyString(unit.TarDemId) === false) {
      const tarDeo = this.getMappingRuleDeo(unit.TarDemId, unit.TarDeoId);
      if (isNullOrUndefined(tarDeo) === false) {
        const tarElements = tarDeo.Elements;
        for (const colmap of colMappings) {
          const ele = tarElements.find(t => t.ID === colmap.ID);
          colmap.Code = ele.Code;
          colmap.Name = ele.Name;
          colmap.DataType = ele.MDataType;
          const a = GspElementTypes.find(t => t.code === Number.parseInt(ele.MDataType.toString()));
          if (isNullOrUndefined(a) === false) {
            colmap.DataType = a.type;
          }
        }

      } else {
        colMappings = new Array<ColumnMapping>();
      }
    }
    unit.ColumnMappingList = colMappings;
    return unit.ColumnMappingList;
  }
  set colMappings(v: ColumnMapping[]) {
    const unit = this.currentTreeNode.mappingNode as MappingUnit;
    unit.ColumnMappingList = v;
    this.currentTreeNode.mappingNode = unit;
  }
  public srcColumns: SrcColumn[]; // = new Array<SrcColumn>();

  // set srcColumns(v: SrcColumn[]) {
  //   this._srcColumns = v;
  //   // const unit = this.currentTreeNode.mappingNode as MappingUnit;
  //   // const srcDeo = this.getMappingRuleDeo(unit.SrcDemId, unit.SrcDeoId);
  //   // if (isNullOrUndefined(srcDeo) === false) {
  //   //   this.srcColumns = srcDeo.Elements;
  //   // } else {
  //   //   console.error('');
  //   // }
  //   // unit.ColumnMappingList = v;
  //   // this.currentTreeNode.mappingNode = unit;
  // }
  srcBe: GSPBusinessEntity;
  tarBe: GSPBusinessEntity;

  constructor() {

  }
  setSrcColumns() {
    // if (isNullOrUndefined(this._srcColumns) === false && this._srcColumns.length > 0) {
    //   return this.srcColumns;
    // }
    if (isNullOrUndefined(this.currentTreeNode) === true || isNullOrUndefined(this.currentTreeNode.mappingNode) === true) {
      this.srcColumns = new Array<SrcColumn>();
    }
    const unit = this.currentTreeNode.mappingNode as MappingUnit;
    if (isNullOrEmptyString(unit.SrcDemId)) {
      this.srcColumns = new Array<SrcColumn>();
    }
    const cols = this.loadSrcColumns(unit.SrcDemId, unit.SrcDeoId);
    this.srcColumns = cols;
  }
  loadSrcColumns(demid: string, deoid: string): SrcColumn[] {
    const srcDeo = this.getMappingRuleDeo(demid, deoid);
    if (isNullOrUndefined(srcDeo) === false) {
      let cols = new Array<SrcColumn>();
      for (const ele of srcDeo.Elements) {
        const srccol = new SrcColumn();
        srccol.ID = ele.ID;
        srccol.Code = ele.Code;
        srccol.Name = ele.Name;
        srccol.ObjectID = srcDeo.ID;
        srccol.ObjectName = srcDeo.Name;
        srccol.FullName = srcDeo.Name + ':' + ele.Name;
        cols.push(srccol);
      }
      if (isNullOrEmptyString(srcDeo.ParentObjectId) === false) {
        const parCols = this.loadSrcColumns(demid, srcDeo.ParentObjectId);
        if (isNullOrUndefined(parCols) === false && parCols.length > 0) {
          cols = cols.concat(parCols);
        }
      }
      return cols;
    } else {
      return new Array<SrcColumn>();
    }
  }
  /**
   * 
   * @param demId 
   * @param isSrc 
   */
  getMappingDem(demId: string): DataExchangeModel {
    let dem: DataExchangeModel = null;
    dem = this.mappingRule.ModelList.find(t => t.ID === demId);
    if (isNullOrUndefined(dem) === true) {
      return null;
    } else {
      return dem;
    }
  }

  /**
   * 
   * @param demId 
   * @param deoId 
   * @param isSrc 
   * @throws 
   */
  getMappingRuleDeo(demId: string, deoId: string): DataExchangeObject {
    let dem: DataExchangeModel = null;
    if (isNullOrEmptyString(demId) === true) {
      console.log('!');
      return;
    }
    dem = this.getMappingDem(demId);
    if (isNullOrUndefined(dem)) {
      console.log('!');
      return;
    }
    if (isNullOrEmptyString(deoId)) {
      // console.log('deoId!');
      return null;
    } else {
      const result = this.findObjectByObjId(deoId, dem.MainObject);
      return result;
    }
  }
  /**
   * 
   * @param dem 
   * @param deoId ID
   */
  getMappingRuleDeoByDem(dem: DataExchangeModel, deoId: string): DataExchangeObject {
    if (isNullOrUndefined(dem)) {
      throw new error('');
    }
    if (isNullOrEmptyString(deoId)) {
      return dem.MainObject;
    } else {
      const result = this.findObjectById(deoId, dem);
      return result;
    }
  }
  public findObjectById(objId: string, dem: DataExchangeModel): DataExchangeObject {
    let result: DataExchangeObject = null;
    result = this.findObjectByObjId(objId, dem.MainObject);
    return result;
  }
  public findObjectByObjId(id: string, deo: DataExchangeObject): DataExchangeObject {
    if (isNullOrEmptyString(id)) {
      return null;
    }
    if (deo.ID === id) {
      return deo;
    }
    if (isNullOrUndefined(deo.ChildrenDeo) || deo.ChildrenDeo.length === 0) {
      return null;
    }
    let result: DataExchangeObject = null;
    for (let i = 0; i < deo.ChildrenDeo.length; i++) {
      if (deo.ChildrenDeo[i].ID === id) {
        result = deo.ChildrenDeo[i];
        return result;
      }
      result = this.findObjectByObjId(id, deo.ChildrenDeo[i]);
      if (result != null) {
        return result;
      }
    }
    return result;
  }

  /**
   * 
   * @param dem 
   */
  addMappingRuleDem(dem: DataExchangeModel) {
    const index = this.mappingRule.ModelList.findIndex(t => t.ID === dem.ID);
    if (index >= 0) {
      return;
    } else {
      this.mappingRule.ModelList.push(dem);
    }
  }
  /**
   * 
   * @param demId ID
   */
  removeMappingRuleDem(demId: String) {
    if (isNullOrEmptyString(demId) === true) {
      return;
    }
    const models = this.mappingRule.ModelList;
    const index = models.findIndex(t => t.ID === demId);
    if (index < 0) {
      return;
    } else {
      models.splice(index, 1);
    }
    this.mappingRule.ModelList = models;
  }
  buildDeoTree(dem: DataExchangeModel): DataExchangeObjectTree {
    if (isNullOrUndefined(dem)) {
      console.warn('');
    }
    const mainObjTree = new DataExchangeObjectTree();
    if (isNullOrUndefined(dem.MainObject) === false) {
      mainObjTree.id = dem.MainObject.ID;
      mainObjTree.name = dem.MainObject.Name;
      mainObjTree.units = new Array<DataExchangeObjectTree>();
      mainObjTree.units = this.buildDeoTreeUnit(dem, dem.MainObject);
    }
    return mainObjTree;
  }
  /**
   * 
   * @param deo 
   */
  private buildDeoTreeUnit(dem: DataExchangeModel, deo: DataExchangeObject): DataExchangeObjectTree[] {
    if (isNullOrUndefined(deo) === false && isNullOrUndefined(deo.ChildrenDeo) === false && deo.ChildrenDeo.length > 0) {
      const units = new Array<DataExchangeObjectTree>();
      for (const obj of deo.ChildrenDeo) {
        const tree = new DataExchangeObjectTree();
        tree.id = obj.ID;
        tree.name = obj.Name;
        tree.units = this.buildDeoTreeUnit(dem, obj);
        units.push(tree);
      }
      return units;
    } else {
      return null;
    }
  }

}



