import { GSPElementDataType } from '@gsp-bef/gsp-be-metadata';


export const GspElementTypes: Array<{ type: GSPElementDataType, code: string | number }> = [
    {
        type: GSPElementDataType.String,
        code: 0
    },
    {
        type: GSPElementDataType.Text,
        code: 1
    },
    {
        type: GSPElementDataType.Integer,
        code: 2
    },
    {
        type: GSPElementDataType.Decimal,
        code: 3
    },
    {
        type: GSPElementDataType.Boolean,
        code: 4
    },
    {
        type: GSPElementDataType.Date,
        code: 5
    },
    {
        type: GSPElementDataType.DateTime,
        code: 6
    },
    {
        type: GSPElementDataType.Binary,
        code: 7
    }
];

