import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';

@Injectable()
export class MappingService {
    private Server_IP: string;
    private Metadata_Base_Path = '/api/dev/main/v1.0/metadatas';
    mappingMetadataInfo = { path: '', code: '' };
    constructor(private http: HttpClient) {
        this.Server_IP = environment.SERVER_IP;
    }
    getMetadata(url: string): Observable<any> {
        return this.http.get<any>(url);
    }
    public LoadMetadata(fullName: string, path: string): Observable<any> {
        const headers = new HttpHeaders();
        headers.append('Content-Type', 'application/json');
        headers.append('Authorization', 'as');
        headers.append('Access-Control-Allow-Origin', '*');
        const options = { params: {}, headers: headers };

        const metadataFullPath = path.replace(/\\/g, '/') + '/' + fullName;
        const encMetadataFullPath = encodeURIComponent(metadataFullPath);
        const url = this.Server_IP + this.Metadata_Base_Path + '/' + encMetadataFullPath;
        return this.http.get(url);
    }
    publishMapping(): Observable<any> {
        const url = this.Server_IP + `/api/dev/main/v1.0/mapping/publish`;
        const body = {
            Mapping: this.mappingMetadataInfo
        };
        return this.http.post(url, body);
    }
}



