import { GSPCommonElement, GSPCommonObject } from '@gsp-bef/gsp-be-metadata';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { DataExchangeElement } from './dataexchange-element';
import { error, isNullOrUndefined } from 'util';

export class DataExchangeObject extends GSPCommonObject {
    public BelongModelId: string;
    public ParentObjectId = '';
    public Elements = new Array<DataExchangeElement>();
    public ChildrenDeo = new Array<DataExchangeObject>();
    protected CreateElement(): GSPCommonElement {
        throw new error('!');
    }
    /**
     * Id
     * @param id Id
     */
    public findObjectByObjId(id: string): DataExchangeObject {
        if (isNullOrEmptyString(id)) {
            return null;
        }
        if (this.ID === id) {
            return this;
        }
        if (isNullOrUndefined(this.ChildrenDeo) || this.ChildrenDeo.length === 0) {
            return null;
        }
        let result: DataExchangeObject = null;
        for (let i = 0; i < this.ChildrenDeo.length; i++) {
            if (this.ChildrenDeo[i].ID === id) {
                result = this.ChildrenDeo[i];
                return result;
            }
            result = this.ChildrenDeo[i].findObjectByObjId(id);
            if (result != null) {
                return result;
            }
        }
        return result;
    }
}



