import { GSPMetadataService, MetadataDto } from '@gsp-lcm/metadata-selector';
import { MappingRuleService } from '../mapping-rule.service';
import { MappingRuleTreeComponent } from './../mapping-rule-tree/mapping-rule-tree.component';
import { MappingRule } from '../model/mapping-rule';

import { Component, OnInit, ViewChild, ComponentFactoryResolver } from '@angular/core';
import { MappingService } from '../mapping.service';
import { isNullOrUndefined } from 'util';
import { MappingPropertyService } from '../mapping-property/mapping-property.service';

@Component({
  selector: 'app-mapping',
  templateUrl: './mapping.component.html',
  styleUrls: ['./mapping.component.css'],
  providers: [
    MappingService,
    MappingRuleService,
    MappingPropertyService
  ]
})
export class MappingComponent implements OnInit {
  @ViewChild('mapping')
  mappingComponent: MappingRuleTreeComponent;
  // mappingId = '0b1467e1-578a-4fc4-b700-c105e51cc5d1';
  mappingRule: MappingRule;
  /**
* 
*/
  private _metadataContent: MetadataDto = null;
  set metadataContent(metadataContent: any) {
    this._metadataContent = metadataContent;
    if (isNullOrUndefined(this._metadataContent)) {
      return;
    }
    this.loadMappingRule(this._metadataContent);
  }
  set metadataUri(url: string) {
    // const mapurl = 'http://localhost:5000/api/dev/main/v1.0/metadatas/mapping%2Fmapping%2FMapping03.map';
    const fileName = url.substring(url.lastIndexOf('/') + 1);
    const filePath = url.substring(1, url.lastIndexOf('/'));
    this.mappingService.LoadMetadata(fileName, filePath).subscribe(value => {
      this.metadataContent = value;
      console.log(this._metadataContent);
    });
  }

  constructor(public mappingService: MappingService, public mappingRuleService: MappingRuleService) { }

  ngOnInit() {
  }
  loadMappingRule(data: MetadataDto) {

    this.mappingComponent.loadMappingRule(data);
  }
  publish() {
    this.mappingComponent.publishMapping();
  }
  save() {
    this._metadataContent.content = JSON.stringify(this.mappingRuleService.mappingRule);
    this.mappingComponent.saveMapping(this._metadataContent);
  }
  cancel() {
    this.mappingComponent.cancel();
  }
}



