import { GSP, Package } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector, Type } from '@angular/core';

import { MappingPackageView } from './views';
import { MappingComponent } from './views/mapping/mapping.component';

export class MappingPackage extends Package {

    private layoutPanel: any;

    constructor(
        gsp: GSP,
        private cfr: ComponentFactoryResolver,
        private injector: Injector
    ) {
        super(gsp);
    }

    activate(state) {
        this.gsp.workspace.addOpener((uri: string) => this.openSg(uri));

        // if (!this.packageView) {
        //   this.packageView = new SgDesignerPackageView(state, this.cfr, this.injector);
        //   this.gsp.workspace.addMainPanel({id: this.id,title:this.data.name, item: this.packageView.getElement()});
        //   const currentElement = this.packageView.getElement().instance as SgDesignerComponent;
        //   if (currentElement) {
        //     currentElement.metadataContent = this.data;
        //   }
        // }
        // this.workspace.activePackage(this);
    }

    openSg(uri) {
        if (uri.endsWith('.map')) {
            const pkgView = new MappingPackageView({}, this.cfr, this.injector);
            const currentElement = pkgView.getElement().instance as MappingComponent;
            currentElement.metadataUri = uri;
            const fileName = uri.substring(uri.lastIndexOf('/') + 1);
            // urititle
            return {
                getTitle: () => fileName,
                getElement: () => pkgView.getElement()
            };
        }
    }
}




