import { Component, OnInit, Input, ViewChild, ElementRef, Output, EventEmitter, OnChanges } from '@angular/core';
import { ElementPropertyConfig } from '../../entity/property-entity';

@Component({
  selector: 'app-property-panel',
  templateUrl: './property-panel.component.html',
  styleUrls: ['./property-panel.component.css']
})
export class PropertyPanelComponent implements OnInit {
  @Input() width = 200;
  @Input() height: number;
  @Input() bounds: HTMLDivElement;
  @ViewChild('east') eastDiv: ElementRef;
  @Output() resizing = new EventEmitter();
  // @ViewChild('panelDrag') draggable: AngularDraggableDirective;

  private originalWidth: number;
  initBarPosition = 0;
  propertyTitleWidth = 100;
  initDragbarLeft = this.propertyTitleWidth + 30;

  isOpen = true;
  currentWidth: number;


  @Input() propertyConfig: ElementPropertyConfig[]; // 
  @Input() propertyData = {}; // 
  @Output() propertyChanged = new EventEmitter<any>(); // 
  @Input() showCloseBtn = true;
  constructor() { }

  ngOnInit() { }

  private changeStatus(item: any) {
    if (!item.status || item.status === 'open') {
      item.status = 'closed';
    } else {
      item.status = 'open';
    }
  }
  // 
  expand() {
    if (!this.isOpen) {
      this.isOpen = true;
      this.width = this.originalWidth = this.currentWidth;
      // this.draggable.minWidth = 240;
      this.updateContainerWidth();
      this.resizing.emit({ right: this.width });
    }
  }

  // 
  collapse() {
    if (this.isOpen) {
      // 
      this.currentWidth = this.width;
      this.isOpen = false;

      this.width = 0;
      this.originalWidth = 0;
      // this.draggable.minWidth = 0;
      // this.draggable.resetPosition();
      this.updateContainerWidth();
      this.resizing.emit({ right: this.width });
    }
  }
  private updateContainerWidth() {
    this.eastDiv.nativeElement.style.width = this.width + 'px';
  }

  /**
   * 
   * @param changeObject
   */
  propertyValueChanged(changeObject) {
    this.propertyChanged.emit(changeObject);
  }

}



