
import { MappingUnit } from '../../model/mapping-unit';
import { MappingRuleService } from '../../mapping-rule.service';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CheckableSettings, TreeItemLookup } from '@progress/kendo-angular-treeview';
import { DataExchangeObjectTree } from '../../model/clientmodel/dataexchangeobject-tree';

@Component({
  selector: 'app-mapping-select-srcdeo',
  templateUrl: './mapping-select-srcdeo.component.html',
  styleUrls: ['./mapping-select-srcdeo.component.css']
})
export class MappingSelectSrcDeoComponent implements OnInit {
  @Output() closeModal = new EventEmitter<any>();
  @Input() value;
  modalConfig = {
    title: '',
    width: 408,
    height: 286
  };
  public objTree: DataExchangeObjectTree[];
  // @Output()
  // objChanged = new EventEmitter<String>();
  private objId = '';
  public checkedKeys: any[] = [];
  expandKeys = [];
  /**
    * Id
    */
  public selectedKeys: any[] = [];
  constructor(private ruleService: MappingRuleService) { }

  ngOnInit() {
    const mappingUnit = this.ruleService.currentTreeNode.mappingNode as MappingUnit;
    if (isNullOrEmptyString(mappingUnit.SrcDemId)) {
      alert('');
      this.closeModal.emit();
      return;
    }
    const dem = this.ruleService.getMappingDem(mappingUnit.SrcDemId);
    this.objTree = [this.ruleService.buildDeoTree(dem)];
    this.expandKeys = [this.objTree[0].id];
    console.log(this.objTree);
  }

  /**
   * 
   */
  selectionChangeHandler(item) {
    console.log(item);
    const selectedObjTree = item.dataItem as DataExchangeObjectTree;
    this.objId = selectedObjTree.id;
  }
  /**
   * 
   */
  public handleChecking(itemLookup: TreeItemLookup): void {
    const selectedObjTree = itemLookup.item.dataItem as DataExchangeObjectTree;
    this.objId = selectedObjTree.id;
  }
  okHandler() {
    if (isNullOrEmptyString(this.objId)) {
      alert('');
      return;
    }
    const mappingUnit = this.ruleService.currentTreeNode.mappingNode as MappingUnit;
    const mainObj = this.ruleService.getMappingRuleDeo(mappingUnit.SrcDemId, this.objId);
    mappingUnit.SrcDeoId = mainObj.ID;
    // TODO   this.mappingService.srcColumns = mainObj.Elements;
    this.ruleService.currentTreeNode.mappingNode = mappingUnit;
    this.ruleService.currentTreeNode.srcDeoName = mainObj.Name;
    const colMappings = this.ruleService.colMappings;
    for (const item of colMappings) {
      item.Expression = '';
      item.DisplayExpression = '';
    }
    this.ruleService.colMappings = colMappings;
    this.ruleService.setSrcColumns();
    this.closeModal.emit(this.objId);
  }
  cancelHandler() {
    this.closeModal.emit();
  }

}



