
import { MappingTypes, ColumnMappingEditType } from '../model/columnmapping-edittype';
import { ColumnMapping } from '../model/column-mapping';
import { MappingRuleService } from '../mapping-rule.service';
import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { isNullOrUndefined } from 'util';

import { MappingUnit } from '../model/mapping-unit';
import { MappingRule } from '../model/mapping-rule';
import { MappingTree } from '../model/clientmodel/mapping-tree';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { DataExchangeElement } from '../model/dataexchange-element';
import { DataExchangeModel } from '../model/dataexchange-model';
import { ElementPropertyConfig, PropertyPanelComponent } from '@farris/ide-devkit';
import { Guid } from '../../libs/Guid';
import { MetadataDto, GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { Observable } from 'rxjs';
import { MappingService } from '../mapping.service';
import { SrcColumn } from '../model/clientmodel/srccolumn';
import { MappingPropertyService } from '../mapping-property/mapping-property.service';
import { CacheService } from '@farris/ide-devkit/services';
import { GridComponent } from '@progress/kendo-angular-grid';
import { ColumnMappingComponent } from './mapping-column-mapping/mapping-column-mapping.component';
import { MappingValidate } from '../util/mapping-validate';

@Component({
  selector: 'app-mapping-rule-tree',
  templateUrl: './mapping-rule-tree.component.html',
  styleUrls: ['./mapping-rule-tree.component.css'],
  providers: [GSPMetadataService]
})
export class MappingRuleTreeComponent implements OnInit {
  @ViewChild('propertyManager')
  propertyManager: PropertyPanelComponent;
  @ViewChild('columnMappingComponent')
  columnMappingComponent: ColumnMappingComponent;
  /**
   * Mapping
   */
  _mappingruleId: string;
  /**
   * Mapping
   */
  isMappingRule = false;
  /**
   * currentTreeNode
   */
  currentTreeNode = new MappingTree();
  /**
   * Id
   */
  public selectedKeys: any[] = [];
  /**
   * Id
   */
  public expandedKeys: any[] = [];
  public data: any[] = [this.mappingRuleService.mappingTree];
  /**
   * Mapping
   */
  public set mappingRule(mappingRule: MappingRule) {
    this._mappingruleId = mappingRule.ID;
    this.isMappingRule = true;
    this.mappingRuleService.mappingRule = mappingRule;
    this.propertyData = mappingRule;
    this.selectedKeys = [mappingRule.ID];
    this.loadMappingTree();
  }
  /**
 * 
 */
  // srcColumns: SrcColumn[] = this.mappingRuleService.srcColumns;
  get srcColumns(): SrcColumn[] { return this.mappingRuleService.srcColumns; }

  /**
   * 
   */
  propertyConfig: ElementPropertyConfig[] = this.propertyService.mappingRuleConfig;
  /**
   * 
   */
  propertyData: any = this.mappingRuleService.mappingRule;
  // _propertyData: any;
  setPropertyConfig() {
    if (isNullOrUndefined(this.mappingRuleService.currentTreeNode) === true) {
      return;
    } else {
      if (this.mappingRuleService.currentTreeNode.isUnit === false) {
        this.propertyConfig = this.propertyService.mappingRuleConfig;
      } else {
        if (this.mappingRuleService.currentTreeNode.level === 1) {
          this.propertyConfig = this.propertyService.mappingUnitConfig;
        } else {
          this.propertyConfig = this.propertyService.mappingUnitDeoConfig;
        }
      }
    }
  }
  setPropertyData() {
    if (isNullOrUndefined(this.mappingRuleService.currentTreeNode)) {
      this.propertyData = new MappingRule();
    }
    this.propertyData = this.mappingRuleService.currentTreeNode.mappingNode;
  }
  setSrcColumns() {
    this.mappingRuleService.setSrcColumns();
  }
  constructor(private formBuilder: FormBuilder,
    private mappingRuleService: MappingRuleService,
    private mappingService: MappingService,
    private cache: CacheService,
    private lcmService: GSPMetadataService, private propertyService: MappingPropertyService) { }

  ngOnInit() {
    this.propertyManager.showCloseBtn = false;
   // this.propertyManager.width = 200;
  }
  /**
   * mapping
   */
  loadMappingTree() {
    const mappingtree = new MappingTree();
    mappingtree.id = this.mappingRuleService.mappingRule.ID;
    mappingtree.code = this.mappingRuleService.mappingRule.Code;
    mappingtree.name = this.mappingRuleService.mappingRule.Name;
    mappingtree.srcDeoName = '';
    mappingtree.tarDeoName = '';
    mappingtree.level = 0;
    mappingtree.mappingNode = this.mappingRuleService.mappingRule;
    this.mappingRuleService.mappingTree = mappingtree;
    this.mappingRuleService.currentTreeNode = mappingtree;
    //  ,
    if (isNullOrUndefined(this.mappingRuleService.mappingRule.RootMappingUnitList) === false
      && this.mappingRuleService.mappingRule.RootMappingUnitList.length > 0) {
      this.loadMappingUnit(this.mappingRuleService.mappingRule.RootMappingUnitList, this.mappingRuleService.currentTreeNode);
      this.expandedKeys.push(mappingtree.id);
    }
    this.data = [mappingtree];
    console.log(this.data);
  }
  /**
   * Mapping
   */
  loadMappingUnit(unitlist: MappingUnit[], treeNode: MappingTree) {
    for (const unit of unitlist) {
      let srcDeoName = '';
      let tarDeoName = '';
      // 
      if (isNullOrEmptyString(unit.SrcDeoId) === false) {
        const srcdeo = this.mappingRuleService.getMappingRuleDeo(unit.SrcDemId, unit.SrcDeoId);
        srcDeoName = srcdeo.Name;
      }
      if (isNullOrEmptyString(unit.TarDeoId) === false) {
        const tardeo = this.mappingRuleService.getMappingRuleDeo(unit.TarDemId, unit.TarDeoId);
        tarDeoName = tardeo.Name;
      }
      const childNode = new MappingTree();
      childNode.id = unit.ID;
      childNode.name = unit.Name;
      childNode.code = unit.Code;
      childNode.srcDeoName = srcDeoName;
      childNode.tarDeoName = tarDeoName;
      childNode.level = treeNode.level + 1;
      childNode.parentNode = treeNode;
      childNode.mappingNode = unit;
      if (isNullOrUndefined(treeNode.units) === true) {
        treeNode.units = new Array<MappingTree>();
      }
      treeNode.units.push(childNode);
      // 
      if (isNullOrUndefined(unit.ChildrenMU) === false && unit.ChildrenMU.length > 0) {
        this.loadMappingUnit(unit.ChildrenMU, childNode);
      }
    }
  }
  /**
   * 
   */
  private copyUnitDef(unit: MappingUnit, mapUnit: MappingUnit, isCopy: boolean) {
    if (isCopy === false) {
      unit.Code = mapUnit.Code + '_' + unit.Order.toString();
      unit.Name = mapUnit.Name + '_' + unit.Order.toString();
    } else {
      unit.Code = unit.Code + '_copy';
      unit.Name = unit.Name + '_';
    }
  }
  /**
   * ,
   */
  addChildTreeNode(mappingUnit: MappingUnit, parentNode: MappingTree) {
    const childTreeNode = mappingUnit.addChildTreeNode(parentNode);
    this.expandedKeys.push(parentNode.id);
    this.selectedKeys = [childTreeNode.id];
    childTreeNode.isUnit = true;
    this.mappingRuleService.currentTreeNode = childTreeNode;
    this.isMappingRule = false;
    console.log(this.mappingRuleService.currentTreeNode);
  }
  /**
   * 
   * @param mappingUnitList 
   * @param parentMu 
   * @param isRuleChild 
   */
  private appendMappingUnitList(mappingUnitList: MappingUnit[], parentMu: MappingUnit, isRuleChild: boolean) {
    const unit = MappingUnit.appendMappingUnitList(mappingUnitList, parentMu, isRuleChild);
    if (isNullOrUndefined(this.mappingRuleService.currentTreeNode.units)) {
      this.mappingRuleService.currentTreeNode.units = new Array<MappingTree>();
    }
    // 
    this.addChildTreeNode(unit, this.mappingRuleService.currentTreeNode);
  }
  /**
   * ,
   */
  deleteFirstLevelTreeNode() {
    const mappingUnit = this.mappingRuleService.currentTreeNode.mappingNode as MappingUnit;
    this.mappingRuleService.removeMappingRuleDem(mappingUnit.TarDemId);
    this.mappingRuleService.removeMappingRuleDem(mappingUnit.SrcDemId);
    const mappingRule = this.mappingRuleService.currentTreeNode.parentNode.mappingNode as MappingRule;
    const num = mappingRule.RootMappingUnitList.findIndex(value => value.ID === this.mappingRuleService.currentTreeNode.id);
    if (num >= 0) {
      mappingRule.RootMappingUnitList.splice(num, 1);
    } else {
      console.error('');
    }
    if (isNullOrUndefined(mappingRule.RootMappingUnitList) === true || mappingRule.RootMappingUnitList.length === 0) {
      this.mappingRuleService.currentTreeNode.parentNode.units = null;
      this.selectedKeys = [mappingRule.ID];
      this.isMappingRule = true;
      this.mappingRuleService.currentTreeNode = this.mappingRuleService.currentTreeNode.parentNode;
    } else {
      const nodeIndex = this.mappingRuleService.currentTreeNode.parentNode.units
        .findIndex(value => value.id === this.mappingRuleService.currentTreeNode.id);
      this.mappingRuleService.currentTreeNode.parentNode.units.splice(nodeIndex, 1);
      let id = '';
      if (mappingRule.RootMappingUnitList.length > num) {
        id = mappingRule.RootMappingUnitList[num].ID;
      } else {
        id = mappingRule.RootMappingUnitList[num - 1].ID;
      }
      this.selectedKeys = [id];
      const index = this.mappingRuleService.currentTreeNode.parentNode.units.findIndex(value => value.id === id);
      this.mappingRuleService.currentTreeNode = this.mappingRuleService.currentTreeNode.parentNode.units[index];
    }
  }
  /**
   * 2+,
   */
  deleteSecondLevelPulsTreeNode() {
    const mappingUnit = this.mappingRuleService.currentTreeNode.parentNode.mappingNode as MappingUnit;
    const deleteId = this.mappingRuleService.currentTreeNode.id;
    const num = mappingUnit.ChildrenMU.findIndex(value => value.ID === deleteId);
    if (num >= 0) {
      mappingUnit.ChildrenMU.splice(num, 1);
    } else {
      console.error('');
    }
    if (isNullOrUndefined(mappingUnit.ChildrenMU) === true || mappingUnit.ChildrenMU.length === 0) {
      this.selectedKeys = [mappingUnit.ID];
      this.mappingRuleService.currentTreeNode.parentNode.units = null;
      this.mappingRuleService.currentTreeNode = this.mappingRuleService.currentTreeNode.parentNode;
    } else {
      let id = '';
      if (mappingUnit.ChildrenMU.length > num) {
        id = mappingUnit.ChildrenMU[num].ID;
      } else {
        id = mappingUnit.ChildrenMU[num - 1].ID;
      }
      this.selectedKeys = [id];
      const deleteIndex = this.mappingRuleService.currentTreeNode.parentNode.units.findIndex(value => value.id === deleteId);
      if (deleteIndex >= 0) {
        this.mappingRuleService.currentTreeNode.parentNode.units.splice(deleteIndex, 1);
      }
      const nodeIndex = this.mappingRuleService.currentTreeNode.parentNode.units.findIndex(value => value.id === id);
      this.mappingRuleService.currentTreeNode = this.mappingRuleService.currentTreeNode.parentNode.units[nodeIndex];

    }
  }

  /***********************************************************************************************/
  /**
   *  
   */
  selectionChangeHandler(item) {
    console.log(item);
    this.mappingRuleService.currentTreeNode = item.dataItem;
    this.mappingRuleService.srcColumns = undefined;
    if (this.mappingRuleService.currentTreeNode.level === 0) {
      this.isMappingRule = true;
      this.mappingRuleService.currentTreeNode.isUnit = false;
    } else {
      this.isMappingRule = false;
      this.mappingRuleService.currentTreeNode.isUnit = true;
    }
    this.setPropertyConfig();
    this.setPropertyData();
    if (this.isMappingRule === false) {
      this.columnMappingComponent.colMappings = this.mappingRuleService.colMappings;
      this.setSrcColumns();
    } else {
      this.columnMappingComponent.colMappings = new Array<ColumnMapping>();
    }
  }
  /**
   * 
   */
  addNodeHandler() {
    if (this.isMappingRule === true) {
      alert('');
      return;
    }
    const mappingUnit = new MappingUnit();
    mappingUnit.initMappingUnit();
    // mapping1
    if (this.mappingRuleService.currentTreeNode.level === 1) {
      const mappingRule = this.mappingRuleService.mappingRule;
      const order = MappingUnit.getOrder(mappingRule.RootMappingUnitList);
      mappingUnit.Order = order;
      this.copyUnitDef(mappingUnit, mappingUnit, false);
      if (this.mappingRuleService.mappingRule.RootMappingUnitList.length >= 1) {
        alert('');
        return;
      } else {
        this.mappingRuleService.mappingRule.RootMappingUnitList.push(mappingUnit);
       // console.log('this.mappingservice.mappingRule:' + this.mappingRuleService.mappingRule);
        this.addChildTreeNode(mappingUnit, this.mappingRuleService.mappingTree);
      }
    } else if (this.mappingRuleService.currentTreeNode.level > 1) { // mapping2+
      const parentUnit = this.mappingRuleService.currentTreeNode.parentNode.mappingNode as MappingUnit;
      if (isNullOrEmptyString(parentUnit.TarDemId)) {
        alert(`${parentUnit.Name}`);
        return;
      }
      if (isNullOrEmptyString(parentUnit.SrcDemId)) {
        alert(`${parentUnit.Name}`);
        return;
      }
      const order = MappingUnit.getOrder(parentUnit.ChildrenMU);
      mappingUnit.Order = order;
      this.copyUnitDef(mappingUnit, parentUnit, false);
      mappingUnit.TarDemId = parentUnit.TarDemId;
      mappingUnit.SrcDemId = parentUnit.SrcDemId;
      parentUnit.ChildrenMU.push(mappingUnit);
      this.addChildTreeNode(mappingUnit, this.mappingRuleService.currentTreeNode.parentNode);
    }
    this.setSrcColumns();
    this.setPropertyConfig();
    this.setPropertyData();
  }
  /**
   * 
   */
  addChildNodeHandler() {
    // 
    if (isNullOrUndefined(this.mappingRuleService.currentTreeNode.parentNode) === false) {
      const unitTemp = this.mappingRuleService.currentTreeNode.mappingNode as MappingUnit;
      if (isNullOrEmptyString(unitTemp.TarDemId)) {
        alert('');
        return;
      }
      if (isNullOrEmptyString(unitTemp.SrcDemId)) {
        alert('');
        return;
      }
      const mappingUnitList = unitTemp.ChildrenMU;
      this.appendMappingUnitList(mappingUnitList, unitTemp, false);
      unitTemp.ChildrenMU = mappingUnitList;
    } else {
      // 
      let mappingUnitList = this.mappingRuleService.mappingRule.RootMappingUnitList;
      if (isNullOrUndefined(mappingUnitList)) {
        mappingUnitList = new Array<MappingUnit>();
      }
      if (mappingUnitList.length >= 1) {
        alert('');
        return;
      } else {
        this.appendMappingUnitList(mappingUnitList, null, true);
        this.mappingRuleService.mappingRule.RootMappingUnitList = mappingUnitList;
      }
    }
    this.setPropertyConfig();
    this.setPropertyData();
    this.setSrcColumns();
  }
  /**
   * 
   */
  copyNodeHandler() {
    if (this.isMappingRule === true) {
      alert('');
      return;
    }
    const mappingUnit = (this.mappingRuleService.currentTreeNode.mappingNode as MappingUnit).unitCopy();
    if (this.mappingRuleService.currentTreeNode.level === 1) {
      // mapping1
      const mappingRule = this.mappingRuleService.mappingRule;
      const order = MappingUnit.getOrder(mappingRule.RootMappingUnitList);
      mappingUnit.Order = order;
      this.copyUnitDef(mappingUnit, mappingUnit, true);
      this.mappingRuleService.mappingRule.RootMappingUnitList.push(mappingUnit);
      console.log('this.mappingservice.mappingRule:', this.mappingRuleService.mappingRule);
      this.addChildTreeNode(mappingUnit, this.mappingRuleService.mappingTree);
    } else if (this.mappingRuleService.currentTreeNode.level > 1) {
      // mapping2+
      const parentUnit = this.mappingRuleService.currentTreeNode.parentNode.mappingNode as MappingUnit;
      const order = MappingUnit.getOrder(parentUnit.ChildrenMU);
      mappingUnit.Order = order;
      this.copyUnitDef(mappingUnit, parentUnit, true);
      parentUnit.ChildrenMU.push(mappingUnit);
      this.addChildTreeNode(mappingUnit, this.mappingRuleService.currentTreeNode.parentNode);
    }
    if (isNullOrUndefined(mappingUnit.ChildrenMU) === false && mappingUnit.ChildrenMU.length > 0) {
      this.loadMappingUnit(mappingUnit.ChildrenMU, this.mappingRuleService.currentTreeNode);
    }
    this.setPropertyConfig();
    this.setPropertyData();
    this.setSrcColumns();
  }
  /**
  * 
  */
  deleteNodeHandler() {
    if (isNullOrUndefined(this.mappingRuleService.currentTreeNode) === true
      || isNullOrUndefined(this.mappingRuleService.currentTreeNode.parentNode) === true) {
      alert('');
      return;
    }
    const result = confirm('?');
    if (result === true) {
      if (this.mappingRuleService.currentTreeNode.level === 1) {
        // 
        this.deleteFirstLevelTreeNode();
      } else if (this.mappingRuleService.currentTreeNode.level > 1) {
        // 
        this.deleteSecondLevelPulsTreeNode();
      }
    }
    this.setPropertyConfig();
    this.setPropertyData();
    this.setSrcColumns();
  }
  public isItemSelected = (_: any, index: string) => this.selectedKeys.indexOf(index) > -1;

  loadMetadataService(): Observable<any> {
    return this.lcmService.LoadMetadata(this.mappingRuleService.mappingMetadata.fileName,
      this.mappingRuleService.mappingMetadata.relativePath);
  }
  /**
   * 
   */
  loadMappingRule(data: MetadataDto) {
    if (isNullOrUndefined(data)) {
      alert('');
      return;
    }
    const rule = JSON.parse(data.content) as MappingRule;
    rule.Code = data.code;
    rule.Name = data.name;
    for (let unit of rule.RootMappingUnitList) {
      unit = this.loadColumnMaping(unit, rule.ModelList);
    }
    this.mappingRuleService.mappingMetadata = data;
    // TODO 
    this.mappingService.mappingMetadataInfo.path = data.relativePath;
    this.mappingService.mappingMetadataInfo.code = data.fileName;

    this.mappingRule = rule;
  }
  /**
   * 
   * @param unit 
   * @param modelList 
   */
  loadColumnMaping(unit: MappingUnit, modelList: DataExchangeModel[]): MappingUnit {
    const columns = unit.ColumnMappingList;
    const dem = modelList.find(t => t.ID === unit.TarDemId);
    const deo = this.mappingRuleService.findObjectById(unit.TarDeoId, dem);
    let order = columns.length + 1;
    for (const col of columns) {
      col.IsSelect = true;
    }
    for (const element of deo.Elements) {
      const index = columns.findIndex(t => t.ID === element.ID);
      if (index < 0) {
        const columnMapping = new ColumnMapping();
        columnMapping.ID = element.ID;
        columnMapping.TargetElementID = element.ID;
        columnMapping.IsSelect = false;
        columnMapping.Code = element.Code;
        columnMapping.Name = element.Name;
        columnMapping.DataType = element.MDataType;
        columnMapping.ExeOrder = order++;
        columnMapping.MappingType = ColumnMappingEditType.FieldMapping;
        columns.push(columnMapping);
      }
    }
    unit.ColumnMappingList = columns;
    if (isNullOrUndefined(unit.ChildrenMU) === false && unit.ChildrenMU.length > 0) {
      for (let childUnit of unit.ChildrenMU) {
        childUnit = this.loadColumnMaping(childUnit, modelList);
      }
    }
    return unit;
  }
  /**
   * 
   */
  public publishMapping() {
    this.mappingService.publishMapping().subscribe(saveResponse => {
      alert('');
    }, error => {
      const head = '';
      const alertInfo = head + '\r\nRequestId:' + error.error.RequestId +
        '\r\nMessage:' + error.error.Message + '\r\nDetail:' + error.error.Detail;
      alert(alertInfo);
      // const errorString = <string>(error.text ? error.text() : error.text);
      // 
      // console.log(error);

    });
  }
  /**
   * 
   */
  public saveMapping(metadataDto: MetadataDto) {
    const rule = this.mappingRuleService.mappingRule;
    // for (let unit of rule.RootMappingUnitList) {
    //   unit = this.saveColumnMapping(unit);
    // }
    if (MappingValidate.checkMappingRule(rule) === false) {
      return;
    }
    metadataDto.content = JSON.stringify(rule);
    const sessionId = this.cache.get('sessionId');
    this.lcmService.SaveMetadata(metadataDto, sessionId).subscribe(saveResponse => {
      alert('');
    }, error => {
      const head = '';
      const alertInfo = head + '\r\n' + error._body;
      alert(alertInfo);

    });
  }
  /**
   * 
   */
  cancel(): any {
    if (confirm('') === true) {
      this.loadMetadataService().subscribe(
        data => {
          this.loadMappingRule(data);
        }, error => {
          console.log(error);
        }
      );
    }

  }
  // saveColumnMapping(unit: MappingUnit): MappingUnit {
  //   const colMappings = unit.ColumnMappingList.filter(t => t.IsSelect === true).sort((a, b) => {
  //     if (a.ExeOrder < b.ExeOrder) {
  //       return -1;
  //     } else {
  //       return 1;
  //     }
  //   });
  //   unit.ColumnMappingList = colMappings;
  //   if (isNullOrUndefined(unit.ChildrenMU) === false && unit.ChildrenMU.length > 0) {
  //     for (let childUnit of unit.ChildrenMU) {
  //       childUnit = this.saveColumnMapping(childUnit);
  //     }
  //   }
  //   return unit;
  // }
}



