
import { MappingTypes, ColumnMappingEditType } from '../../model/columnmapping-edittype';
import { ColumnMapping } from '../../model/column-mapping';
import { MappingRuleService } from '../../mapping-rule.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { MappingTree } from '../../model/clientmodel/mapping-tree';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { MappingService } from '../../mapping.service';
import { SrcColumn } from '../../model/clientmodel/srccolumn';
import { MappingPropertyService } from '../../mapping-property/mapping-property.service';
import { CacheService } from '@farris/ide-devkit/services';
import { GridComponent } from '@progress/kendo-angular-grid';

@Component({
    selector: 'app-mapping-column-mapping',
    templateUrl: './mapping-column-mapping.component.html',
    styleUrls: ['./mapping-column-mapping.component.css'],
    providers: [GSPMetadataService]
})
export class ColumnMappingComponent implements OnInit {
    @ViewChild('colMappingGrid')
    colMappingGrid: GridComponent;
    /**
     * 
     */
    public get colMappings(): ColumnMapping[] {
        return this.mappingRuleService.colMappings;
    }

    /**
     * 
     */
    public set colMappings(v: ColumnMapping[]) {
        this.mappingRuleService.colMappings = v;
    }
    /**
      * 
      */
    colMappingSelection = [];
    /**
     * 
     */
    filterCondition = '';
    /**
     * 
     */
    mappingTypes = MappingTypes;
    /**
   * 
   */
    // srcColumns: SrcColumn[] = this.mappingRuleService.srcColumns;
    get srcColumns(): SrcColumn[] { return this.mappingRuleService.srcColumns; }

    setSrcColumns() {
        this.mappingRuleService.setSrcColumns();
    }
    constructor(private formBuilder: FormBuilder,
        private mappingRuleService: MappingRuleService,
        private mappingService: MappingService,
        private cache: CacheService,
        private lcmService: GSPMetadataService, private propertyService: MappingPropertyService) { }

    ngOnInit() {
    }
    /**
 * 
 * @param param
 */
    cellClickHandler({ sender, rowIndex, columnIndex, dataItem, isEdited }) {
        if (!isEdited) {
            sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
        }
    }
    /**
     * 
     * @param args
     */
    cellCloseHandler(args: any) {
        const { formGroup, dataItem } = args;
        // 
        // Object.assign(dataItem, formGroup.value);
        const num = this.colMappings.findIndex(col => col.ID === dataItem.ID);
        const colmunMappings = this.colMappings;
        colmunMappings[num] = dataItem;
        this.colMappings = colmunMappings;
    }
    /**
     * 
     * @param value 
     */
    mappingTypeChangeHandler(value: ColumnMappingEditType) {
        if (isNullOrUndefined(this.colMappingSelection) === false && this.colMappingSelection.length === 0) {
            console.error('');
            return;
        }
        const col = this.colMappings[this.colMappingSelection[0]];
        col.MappingType = value;
        col.Expression = '';
        col.DisplayExpression = '';
        if (col.MappingType === ColumnMappingEditType.CreateGuid) {
            col.Expression = 'GUID';
        }
        this.colMappings[this.colMappingSelection[0]] = col;
        // console.log(' this.colmappings:', this.colMappings);
    }
    /**
     * 
     * @param value 
     */
    mappingExpressionChangeHandler(value) {
        const col = this.colMappings[this.colMappingSelection[0]];
        const srccol = this.srcColumns.find(t => t.ID === value);
        col.Expression = value;
        col.DisplayExpression = '';
        col.SrcDeoId = srccol.ObjectID;
        this.colMappings[this.colMappingSelection[0]] = col;
        console.log(' this.colmappings:', this.colMappings);
    }
    /**
     * 
     * @param e 
     */
    public onSelectedKeysChange(e) {
        this.colMappingSelection = [e];
    }

    /**
     * 
     * @param dataItem 
     */
    public createFormGroup(dataItem: any): FormGroup {
        return this.formBuilder.group({
            'IsSelect': dataItem.IsSelect,
            'ID': dataItem.ID,
            'ExeOrder': dataItem.ExeOrder,
            'TargetElementID': dataItem.TargetElementID,
            'Code': dataItem.Code,
            'Name': dataItem.Name,
            'DataType': dataItem.DataType,
            'DisplayExpression': dataItem.DisplayExpression,
            'Expression': dataItem.Expression,
            'MappingType': dataItem.MappingType
        });
    }
    /**
       * 
       */
    moveUpHandler() {
        const num = this.colMappingSelection[0] as number;
        if (num === 0) {
            return;
        }
        const colMap = this.colMappings[num];
        this.colMappings[num] = this.colMappings[num - 1];
        this.colMappings[num].ExeOrder = 1 - (-num);
        this.colMappings[num - 1] = colMap;
        this.colMappings[num - 1].ExeOrder = num;
        this.colMappingSelection = [num - 1];
    }
    /**
     * 
     */
    moveDownHandler() {
        const num = this.colMappingSelection[0] as number;
        if (num === this.colMappings.length - 1) {
            return;
        }
        const colMap = this.colMappings[num];
        this.colMappings[num] = this.colMappings[num - (-1)];
        this.colMappings[num].ExeOrder = 1 - (-num);
        this.colMappings[num - (-1)] = colMap;
        this.colMappings[num - (-1)].ExeOrder = 2 - (-num);
        this.colMappingSelection = [num - (-1)];
    }
    selectAllHandler() {
        const colMaps = this.colMappings;
        for (const colMap of colMaps) {
            colMap.IsSelect = true;
        }
        this.colMappings = colMaps;
    }
    selectNoneHandler() {
        const colMaps = this.colMappings;
        for (const colMap of colMaps) {
            colMap.IsSelect = false;
        }
        this.colMappings = colMaps;
    }
    autoMatchHandler() {
        if (isNullOrUndefined(this.colMappings) === true || this.colMappings.length === 0) {
            alert('');
        }
        if (isNullOrUndefined(this.srcColumns) === true || this.srcColumns.length === 0) {
            alert('');
        }
        const colMaps = this.colMappings;
        for (const colMap of colMaps) {
            const srccol = this.srcColumns.find(t => t.Code === colMap.Code);
            colMap.MappingType = ColumnMappingEditType.FieldMapping;
            if (isNullOrUndefined(srccol) === false) {
                colMap.Expression = srccol.ID;
                colMap.SrcDeoId = srccol.ObjectID;
            }
        }
        this.colMappings = colMaps;
    }
    /**
     * 
     */
    filterHandler() {
        const colMaps = this.colMappings;
        if (isNullOrEmptyString(this.filterCondition) === false) {
            const filterCol = colMaps.filter(t =>
                ((t.Code.toUpperCase()).indexOf(this.filterCondition.toUpperCase()) >= 0 ||
                    (t.Name.toUpperCase()).indexOf(this.filterCondition.toUpperCase()) >= 0)
            );
            this.colMappingGrid.data = filterCol;
        } else {
            this.colMappingGrid.data = this.mappingRuleService.colMappings;
            //  this.setSrcColumns();
        }
    }
}



