import { ColumnMapping } from './../../model/column-mapping';
import { MappingUnit } from '../../model/mapping-unit';
import { MappingRuleService } from '../../mapping-rule.service';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CheckableSettings, TreeItemLookup } from '@progress/kendo-angular-treeview';
import { DataExchangeObjectTree } from '../../model/clientmodel/dataexchangeobject-tree';
import { ColumnMappingEditType } from '../../model/columnmapping-edittype';
@Component({
  selector: 'app-mapping-select-tardeo',
  templateUrl: './mapping-select-tardeo.component.html',
  styleUrls: ['./mapping-select-tardeo.component.css']
})
export class MappingSelectTarDeoComponent implements OnInit {
  @Output() closeModal = new EventEmitter<any>();
  @Input() value;
  modalConfig = {
    title: '',
    width: 408,
    height: 286
  };

  public objTree: DataExchangeObjectTree[];
  // @Output()
  // objChanged = new EventEmitter<String>();
  private objId = '';
  public checkedKeys: any[] = [];
  expandKeys = [];
  /**
    * Id
    */
  public selectedKeys: any[] = [];
  constructor(private mappingService: MappingRuleService) { }

  ngOnInit() {
    const mappingUnit = this.mappingService.currentTreeNode.mappingNode as MappingUnit;
    if (isNullOrEmptyString(mappingUnit.TarDemId)) {
      alert('');
      this.closeModal.emit();
     // return;
    } else {
      const dem = this.mappingService.getMappingDem(mappingUnit.TarDemId);
      this.objTree = [this.mappingService.buildDeoTree(dem)];
      this.expandKeys = [this.objTree[0].id];
      console.log(this.objTree);
    }

  }

  /**
  *  
  */
  selectionChangeHandler(item) {
    console.log(item);
    const selectedObjTree = item.dataItem as DataExchangeObjectTree;
    this.objId = selectedObjTree.id;
  }
  /**
   * 
   * @param itemLookup
   */
  public handleChecking(itemLookup: TreeItemLookup): void {
    const selectedObjTree = itemLookup.item.dataItem as DataExchangeObjectTree;
    this.objId = selectedObjTree.id;
  }
  okHandler() {
    if (isNullOrEmptyString(this.objId)) {
      alert('');
      return;
    }
    const mappingUnit = this.mappingService.currentTreeNode.mappingNode as MappingUnit;
    const mainObj = this.mappingService.getMappingRuleDeo(mappingUnit.TarDemId, this.objId);
    mappingUnit.TarDeoId = mainObj.ID;
    const columnMappings = new Array<ColumnMapping>();
    for (let index = 0; index < mainObj.Elements.length; index++) {
      const element = mainObj.Elements[index];
      const columnMapping = new ColumnMapping();
      columnMapping.ID = element.ID;
      columnMapping.TargetElementID = element.ID;
      columnMapping.IsSelect = true;
      columnMapping.Code = element.Code;
      columnMapping.Name = element.Name;
      columnMapping.DataType = element.MDataType;
      columnMapping.ExeOrder = index + 1;
      columnMapping.MappingType = ColumnMappingEditType.FieldMapping;
      columnMappings.push(columnMapping);
    }
    mappingUnit.ColumnMappingList = columnMappings;
    this.mappingService.currentTreeNode.mappingNode = mappingUnit;
    this.mappingService.currentTreeNode.tarDeoName = mainObj.Name;
    this.closeModal.emit(this.objId);
  }
  cancelHandler() {
    this.closeModal.emit();
  }

}



