import { MappingTree } from './clientmodel/mapping-tree';
import { isNullOrUndefined } from 'util';
import { ColumnMapping } from './column-mapping';
import { GroupEvidence } from './group-evidence';
import { ServiceComponent } from './service-component';
import { Guid } from '../../libs/Guid';


/**
* Mapping
*/
export class MappingUnit {
    /**
     * 
     */
    public ID: string;
    /**
     * 
     */
    public Code: string;
    /**
     * 
     */
    public Name: string;
    /**
     * 
     */
    public Description: string;
    /**
     * deid
     */
    public SrcDemId: string;
    /**
     * dEID
     */
    public TarDemId: string;
    /**
     * deid
     */
    public SrcDeoId: string;
    /**
     * dEID
     */
    public TarDeoId: string;
    /**
     * 
     */
    public Order: number;
    /**
     * 
     */
    public ExecuteCondition: string;
    /**
     * 
     */
    public GroupBy = new Array<GroupEvidence>();
    /**
     * 
     */
    public ColumnMappingList = new Array<ColumnMapping>();
    /**
     * 
     */
    public ChildrenMU = new Array<MappingUnit>();
    /**
     * 
     */
    public DoBefore = new Array<ServiceComponent>();
    /**
     * 
     */
    public DoAfter = new Array<ServiceComponent>();
    /**
     * 
     * @param mappingUnitList 
     * @param parentMu 
     * @param isRuleChild 
     */
    public static appendMappingUnitList(mappingUnitList: MappingUnit[], parentMu: MappingUnit, isRuleChild: boolean): MappingUnit {
        // 
        const unit = new MappingUnit();
        unit.initMappingUnit();
        let code = unit.Code;
        let name = unit.Name;
        if (isNullOrUndefined(parentMu) === false) {
            code = parentMu.Code;
            name = parentMu.Name;
        }
        // ,
        if (isNullOrUndefined(mappingUnitList) === true) {
            mappingUnitList = new Array<MappingUnit>();
            unit.Order = 1;
            unit.Code = code + '_' + unit.Order;
            unit.Name = name + '_' + unit.Order;
        } else {
            if (mappingUnitList.length === 0) {
                unit.Order = 1;
            } else {
                unit.Order = this.getOrder(mappingUnitList);
            }
            unit.Code = code + '_' + unit.Order;
            unit.Name = name + '_' + unit.Order;
        }
        if (isRuleChild === false && isNullOrUndefined(parentMu) === false) {
            unit.TarDemId = parentMu.TarDemId;
            unit.SrcDemId = parentMu.SrcDemId;
        }
        mappingUnitList.push(unit);
        return unit;
    }
    /**
 * 
 */
    public static getOrder(list: MappingUnit[]): number {
        let i = -1;
        for (const unit of list) {
            if (unit.Order > i) {
                i = unit.Order;
            }
        }
        return i + 1;
    }
    /**
 * 
 */
    public initMappingUnit() {
        this.ID = Guid.newGuid();
        this.Code = 'MappingUnit';
        this.Name = '';
    }
    /**
     * ,id
     */
    public unitCopy(): MappingUnit {
        const unit = new MappingUnit();
        unit.ID = Guid.newGuid();
        unit.Code = this.Code;
        unit.Name = this.Name;
        unit.Description = this.Description;
        unit.SrcDemId = this.SrcDemId;
        unit.SrcDeoId = this.SrcDeoId;
        unit.TarDemId = this.TarDemId;
        unit.TarDeoId = this.TarDeoId;
        unit.Order = this.Order;
        unit.ExecuteCondition = this.ExecuteCondition;
        if (isNullOrUndefined(this.GroupBy) === false) {
            unit.GroupBy = new Array<GroupEvidence>();
            for (const groupEvidence of this.GroupBy) {
                unit.GroupBy.push(groupEvidence);
            }
        }
        if (isNullOrUndefined(this.ColumnMappingList) === false) {
            const list = new Array<ColumnMapping>();
            for (const item of this.ColumnMappingList) {
                const newitem = new ColumnMapping();
                newitem.ID = Guid.newGuid();
                newitem.ExeOrder = item.ExeOrder;
                newitem.TargetElementID = item.TargetElementID;
                newitem.DisplayExpression = item.DisplayExpression;
                newitem.Expression = item.Expression;
                newitem.MappingType = item.MappingType;
                list.push(newitem);
            }
            unit.ColumnMappingList = list;
        }
        unit.DoAfter = this.DoAfter;
        unit.DoBefore = this.DoBefore;

        if (isNullOrUndefined(this.ChildrenMU) === false) {
            // 
            unit.ChildrenMU = this.copyMapUnitlist(this.ChildrenMU);
        }
        return unit;
    }
    /**
     * 
     */
    private copyMapUnitlist(mapUnitlist: MappingUnit[]): MappingUnit[] {
        const unitList = new Array<MappingUnit>();
        for (const unit of mapUnitlist) {
            const mapUnit = unit.unitCopy();
            unitList.push(mapUnit);
        }
        return unitList;
    }
    /**
  * ,
  */
    public addChildTreeNode(parentNode: MappingTree): MappingTree {
        const childTreeNode = new MappingTree();
        childTreeNode.id = this.ID;
        childTreeNode.code = this.Code;
        childTreeNode.name = this.Name;
        childTreeNode.srcDeoName = this.SrcDeoId;
        childTreeNode.tarDeoName = this.TarDeoId;
        childTreeNode.parentNode = parentNode;
        childTreeNode.level = parentNode.level + 1;
        childTreeNode.mappingNode = this;
        parentNode.units.push(childTreeNode);
        return childTreeNode;
    }
}



