import { SrcColumn } from './../model/clientmodel/srccolumn';
import { MappingRuleService } from '../mapping-rule.service';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { MappingUnit } from '../model/mapping-unit';
import { DataExchangeElement } from '../model/dataexchange-element';
import { isNullOrUndefined } from 'util';
import { MappingTypes } from '../model/columnmapping-edittype';
import { Pipe, PipeTransform } from '@angular/core';


/**
 * Mapping
 */
@Pipe({ name: 'mappingTypeConvert' })
export class MappingTypeConvert implements PipeTransform {
    transform(mappingType: any): string {
        if (isNullOrUndefined(mappingType)) { console.error('mappingType'); }
        return MappingTypes.find(item => item.mappingType.toString() === mappingType.toString()).name;
    }
}
/**
 * 
 */
@Pipe({ name: 'mappingExpressionConvert' })
export class MappingExpressionConvert implements PipeTransform {
    transform(expression: string, elements: SrcColumn[]): string {
        if (isNullOrUndefined(expression)) { console.log('expression'); }
        if (isNullOrUndefined(elements)) {
            return;
        }
        const ele = elements.find(item => item.ID === expression);
        if (isNullOrUndefined(ele)) {
            return '';
        } else {
            return ele.FullName;
        }
    }
}
/**
 * 
 */
@Pipe({
    name: 'mappingUnitDeoConvert'
})
export class MappingUnitDeoConvert implements PipeTransform {
    constructor(private service: MappingRuleService) { }

    transform(deoId: string, demId: string): string {
        if (isNullOrEmptyString(demId) || isNullOrEmptyString(deoId)) {
            return '';
        }
        const deo = this.service.getMappingRuleDeo(demId, deoId);
        return deo.Name;
    }
}



