import { MappingBEInfo } from './mapping-beinfo';
import { ServiceComponent } from './service-component';
import { MappingUnit } from './mapping-unit';
import { DataExchangeModel } from './dataexchange-model';

/**
* Mapping
*/
export class MappingRule {
    /**
    * 
    */
    public ID: string;
    /**
    * 
    */
    public Code: string;
    /**
    * 
    */
    public Name: string;
    /**
    * 
    */
    public Description: string;
    /**
     * 
    */
    public ModelList = new Array<DataExchangeModel>();

    /**
    * 
    */
    public RootMappingUnitList = new Array<MappingUnit>();
    /**
   * 
   */
    public DoBefore = new Array<ServiceComponent>();
    /**
     * 
     */
    public DoAfter = new Array<ServiceComponent>();
    /**
    * 
    */
    public Creator: string;
    /**
    * 
    */
    public CreatedTime = new Date;
    /**
    * 
    */
    public LastModifier: string;
    /**
    * 
    */
    public LastModifiedTime = new Date;
}



