import {
  Component, OnInit, Input, ComponentFactoryResolver, ViewChild, ViewContainerRef, OnChanges, Output, EventEmitter
} from '@angular/core';
import { PropertyItemComponent } from '../property-item/property-item.component';
import { PropertyEntity, ElementPropertyConfig } from '../../entity/property-entity';

@Component({
  selector: 'webide-property-item-list',
  templateUrl: './property-item-list.component.html',
  styleUrls: ['./property-item-list.component.css']
})
export class PropertyItemListComponent implements OnInit, OnChanges {
  @Input() category: ElementPropertyConfig; // 
  @Input() propertyData = {}; // 
  @Output() valueChanged = new EventEmitter<any>();
  @ViewChild('panelItemContainer', { read: ViewContainerRef }) panelItemContainer: ViewContainerRef;

  // 
  // searchValue = '';
  // searchTimer;

  constructor(private componentFactoryResolvor: ComponentFactoryResolver) { }

  ngOnInit() { }

  ngOnChanges() {
    this.createPropertyPanel();
  }

  /**
   * panel
   */
  private createPropertyPanel() {
    const properties = this.category.properties;
    if (!this.panelItemContainer || !properties || !this.propertyData || properties.length === 0) {
      return;
    }
    this.panelItemContainer.clear();

    properties.forEach(element => {
      const itemPanel = this.componentFactoryResolvor.resolveComponentFactory(PropertyItemComponent);
      const propertyRef = this.panelItemContainer.createComponent(itemPanel);
      propertyRef.instance.elementConfig = element; // 
      propertyRef.instance.elementValue = this.propertyData[element.propertyID]; // 

      // data
      propertyRef.instance.valueChanged.subscribe((changeObject) => {
        this.propertyData[changeObject.propertyID] = changeObject.propertyValue;

        if (this.category.setPropertyRelates && typeof (this.category.setPropertyRelates) === 'function') {
          this.category.setPropertyRelates(changeObject.propertyID, changeObject.propertyValue);
        }
        this.valueChanged.emit(changeObject);
      });
    });
  }

  /**
   * 
   */
  // private changeSearchValue() {
  //   if (this.searchTimer) {
  //     window.clearTimeout(this.searchTimer);
  //   }
  //   this.searchTimer = setTimeout(() => this.searchProperty(), 500);
  // }

  /**
   * 
   */
  // private searchProperty() {
  //   this.componentRefList.forEach(componentRef => {
  //     const config = componentRef.instance.elementConfig;
  //     const name = this.type === 'property' ? config.propertyName : config.eventName;
  //     if (this.searchValue === '') {
  //       componentRef.instance.isHide = false;
  //     } else if (name.indexOf(this.searchValue) < 0) {
  //       componentRef.instance.isHide = true;
  //     } else {
  //       componentRef.instance.isHide = false;
  //     }
  //   });
  // }

}



