import {
  Component, OnInit, Input, Output,
  EventEmitter, ComponentFactoryResolver, Injector
} from '@angular/core';
import { PropertyEntity } from '../../entity/property-entity';
import { BsModalService } from '@farris/ui/container/modal';

@Component({
  selector: 'webide-property-item',
  templateUrl: './property-item.component.html',
  styleUrls: ['./property-item.component.css']
})
export class PropertyItemComponent implements OnInit {
  @Input() elementConfig: PropertyEntity;
  @Input() elementValue: any;
  @Input() isHide: boolean;
  @Output() valueChanged = new EventEmitter<any>();
  elementShowValue: any; // :
  selectOptions = [];
  itemType = 'string';

  numberRestriction = {
    format: 'n2',
    min: '',
    max: ''
  };
  constructor(private resolver: ComponentFactoryResolver, private modalService: BsModalService, private injector: Injector) { }

  ngOnInit() {
    const propertyType = this.itemType = this.elementConfig.propertyType;
    switch (propertyType) {
      case 'boolean': {
        this.itemType = 'select';
        this.selectOptions = [{ key: true, value: true }, { key: false, value: false }];
        break;
      }
      case 'select': {
        this.selectOptions = this.elementConfig.iterator;
        break;
      }
      case 'modal': {
        if (this.elementConfig.converter) {
          this.elementShowValue = this.elementConfig.converter.convertTo(this.elementValue);
        } else if (this.elementValue) {
          this.elementShowValue = JSON.stringify(this.elementValue);
        }
        break;
      }
      case 'number': {
        const precision = this.elementConfig.precision;
        if (precision !== undefined && precision > -1) {
          this.numberRestriction.format = 'n' + (precision === 0 ? '' : precision);
        }
        this.numberRestriction.min = this.elementConfig.min || '';
        this.numberRestriction.max = this.elementConfig.max || '';

      }
    }
  }

  /**
   * 
   */
  changeValue() {
    const object = {
      propertyID: this.elementConfig.propertyID,
      propertyValue: this.elementValue
    };
    this.valueChanged.emit(object);
  }

  /**
   * 
   */
  openModal() {
    this.createEditorComponent();
  }

  /**
   * 
   */
  private createEditorComponent() {
    const editor = this.elementConfig.editor;
    if (!editor) {
      return;
    }
    // 
    const compFactory = this.resolver.resolveComponentFactory(editor);
    const compRef = compFactory.create(this.injector);
    compRef.instance.value = this.elementValue;
    if (this.elementConfig.editorParams && compRef.instance.editorParams) {  // 
      compRef.instance.editorParams = this.elementConfig.editorParams;
    }
    let modalConfig = compRef.instance.modalConfig;
    if (!modalConfig) {
      modalConfig = {
        title: '',
        width: 800,
        height: 400,
        showButtons: false
      };
    } else {
      modalConfig.showButtons = true;
      modalConfig.buttons = compRef.instance.modalFooter;
    }

    const dialog = this.modalService.show(compRef, modalConfig);

    // 
    if (!compRef.instance.closeModal) {
      return;
    }
    compRef.instance.closeModal.subscribe((data) => {
      // 
      if (data) {
        if (!this.elementConfig.converter) {
          this.elementShowValue = JSON.stringify(data);
        } else {
          this.elementShowValue = this.elementConfig.converter.convertTo(data);
        }
        const changeObject = {
          propertyID: this.elementConfig.propertyID,
          propertyValue: data
        };
        this.valueChanged.emit(changeObject);
      }
      dialog.close();
    });
  }
}



